<template>
	<ul>
		<li v-for="(detail,index) in Details" :key="index">
			<a class="a2 linksGoods">
				<span class="adc">
					<img class="pic" :src="detail.imgurl" />
					<span v-if="detail.rest"><var></var></span>
				</span>
				<dl>
					<dt>{{detail.name}}</dt>
					<dd class="adf"><strong class="line_pre">月售{{detail.number}}件</strong></dd>
					<dd v-if="detail.type"><span class="a2k" style="background:#5FBC65;">{{detail.type}}</span></dd>
					<dd class="a3p">
						<label><em>￥</em>{{detail.sprice}}</label>
						<strong class="a3q"><em>￥</em>{{detail.price}}</strong>
					</dd>
				</dl>
			</a>
			<div class="Box">
				<span class="reduce" v-show="detail.num>0" @click.stop.prevent="sub(detail)"></span>
				<label v-show="detail.num>0">{{nums[detail.id]}}</label>
				<span class="add" @click.stop.prevent="add(detail)"></span>
			</div>
		</li>
	</ul>
</template>

<script>
	
	/*引入模块*/
	import $ from "jquery/dist/jquery.min.js";
	import store from "store/dist/store.everything.min.js";
	
	export default {
		name: 'goodList',
		props:['shopid'],
		data (){
			return {
				Details:[],
				nums:{}
			}
		},
		created(){
			var goodlistUrl='https://easy-mock.com/mock/5a051583034de12ca203602f/jdata/goodsList';
			var th=this;
			$.get(goodlistUrl,{},function (res){
				for (var i=0;i<res.details.length;i++){
					th.Details.push(res.details[i]);
				}
			});
			
			//监听addcar广播事件，接受数据
		    this.$root.bus.$on('addcar',function(value){
			    for(var i=0;i<th.Details.length;i++){
			        if(value.id == th.Details[i].id){
				        th.Details.splice(i,1,value);
				        th.nums[th.Details[i].id] = th.Details[i].num;    //让nums的数据与传来的数据一致       
				        return;
			        }	
			    }
		    });
		
		    //监听subcar广播事件，接受数据
		    this.$root.bus.$on('subcar',function(value){
			    for(var i=0;i<th.Details.length;i++){
			        if(value.id == th.Details[i].id){
				        th.Details.splice(i,1,value);
				        th.nums[th.Details[i].id] = th.Details[i].num;  //让nums的数据与传来的数据一致 
				        return;
			        }
			    }
		    });
			
		},
		methods:{
			add:function (detail){
				
				/*获取缓存数据*/
				var car = store.get("car");
				
				/*判断用户是否在这个商家添加过商品*/
				if (car[this.shopid]){
					
					/*判断此商品是否添加过*/
					for (let i = 0;i < car[this.shopid].length; i++){
						let cur = car[this.shopid][i];
						if (detail.id == cur.id) {
							
							/*设置添加数量不能超过库存数*/
				            if (this.nums[detail.id] < detail.tnum) {
				            	
				            	/*如果添加过,则商品数量+1*/
					            car[this.shopid][i].num += 1;
					            this.$set(this.nums, detail.id, car[this.shopid][i].num);
					            
					            /*向car组件传递数据*/
					            detail.num = car[this.shopid][i].num;
					            this.$root.bus.$emit("add", detail);
				            }
				            store.set("car", car);
				            return false;
				        }
					}
					
					/*如果没有找到同类商品,则添加*/
					detail.num = 1;
			        car[this.shopid].push(detail);
			        store.set("car", car);
			        
				}else{
					
					/*如果没有在该商家添加过商品,则以商家id创建一个子购物车*/
					car[this.shopid] = [];
					
					/*添加商品信息,将商品放入购物车*/
			        detail.num = 1;
			        car[this.shopid].push(detail);
			        
			       /* 更新缓存*/
			        store.set("car", car);
				}
				this.$set(this.nums, detail.id, 1);
				
				/*设置自定义事件*/
			    this.$root.bus.$emit("add", detail);
			},
			
			sub:function (detail){
				/*获取缓存数据*/
				var car = store.get("car");
				
			    for (let i = 0; i < car[this.shopid].length; i++) {
			    	
			    	/*获取对应商品*/
			        if (detail.id == car[this.shopid][i].id) {
			        	
			        	/*将缓存中的商品数量-1*/
				        car[this.shopid][i].num -= 1;
				        
				        /*显示界面中的商品数量-1*/
				        this.nums[detail.id] -= 1;
				        
				        detail.num = car[this.shopid][i].num;
				        
				        if(car[this.shopid][i].num <= 0) {
							//若商品数量减至0，则删除cars和缓存中的此条数据
				            car[this.shopid].splice(i, 1);
						}
				        
				        /*更新缓存*/
				        store.set("car", car);
				        
				        /*自定义事件,广播传递数据*/
				       
				        this.$root.bus.$emit("sub", detail);
				        return false;
			        }
			    }
			}
		}
	}
</script>

<style lang="scss">
	
</style>


